import Loading from '../components/Loading/Loading.vue';
export default {
	data() {
		return {
			demoDate: ["H", "C", 'O', 'D', 'E', 'R', 'D', 'E', 'M', 'O', 'T', 'E', 'S', 'T'],
			loadingType : 0,
			isEnd : false,
			page:1
		}
	},
	onLoad: function (options) {
		
	},
	onBackPress:function(){
		this.page = 1;
		this.loading = false;
		this.loadingType = 0;
		this.isEnd = false;
	},
	onReachBottom : function(){
		//避免多次触发
		if (this.loadingType == 1 || this.isEnd){return ;}
		this.loadMoreFunc();
	},
	methods:{
		//加载更多时执行的函数
		loadMoreFunc: function (){
			//假设 this.page > 2 代表加载了全部数据
			// 实际开的过程以 api 接口返回数据为准
			if (this.page > 2){
				this.isEnd = true;
				this.loadingType = 2;
				return ;
			}
			//展示loading
			this.loadingType = 1;
			//追加数据(延迟1秒 模拟网络请求)
			setTimeout(()=>{
				this.loading  = false;
				var newData    = this.getArrRandomly(this.demoDate);
				this.demoDate = this.demoDate.concat(newData);
				//累加页码
				this.page++;
				this.loadingType = 0;
			}, 1000);
		},
		getArrRandomly:(arr)=>{
			var len = arr.length;
			for (var i = 0; i < len; i++) {
				var randomIndex = Math.floor(Math.random() * (len - i));
				var itemAtIndex = arr[randomIndex];
				arr[randomIndex] = arr[i];
				arr[i] = itemAtIndex;
			}
			return arr;
		}
	},
	components: {
		Loading
	}
}
